.hero-image {
    /* Use linear-gradient to add a darken background effect to the image */
    background-image: url("https://www.google.com/url?sa=i&url=https%3A%2F%2Fwww.latimes.com%2Fsports%2Flakers%2Fstory%2F2025-04-11%2Flebron-james-injury-lakers-clinch-third-seed-west-rockets&psig=AOvVaw3acyvgp7aW4pCedoqsFuzA&ust=1745216195134000&source=images&cd=vfe&opi=89978449&ved=0CBUQjRxqFwoTCJjK7oz75YwDFQAAAAAdAAAAABAE");
    height: 80vh; /* Set a specific height */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
  }
  
  .hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgb(255, 255, 255);
    width: 80%;
    max-width: 800px;
  }
  
  .hero-text h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    font-weight: bold;
  }
  
  .hero-text p {
    font-size: 1.2rem;
    margin-bottom: 2rem;
  }
  
  .hero-button {
    padding: 0.8rem 2rem;
    background-color: #f44336;
    color: white;
    border: none;
    border-radius: 4px;
    font-size: 1rem;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .hero-button:hover {
    background-color: #d32f2f;
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .hero-text h1 {
      font-size: 2rem;
    }
    
    .hero-text p {
      font-size: 1rem;
    }
  }
  
  @media (max-width: 480px) {
    .hero-image {
      height: 60vh;
    }
    
    .hero-text h1 {
      font-size: 1.5rem;
    }
  }
  